@use 'variables' as v;
@use 'mixins' as m;

.borde{  
    
    border-bottom: 1rem solid #232734;
    width: 100%;
    margin-top: 5rem;
    margin-bottom: -6.5rem;
}
.border-top{
    border-top: none; 
}

.text-center{
    text-align: center;
}

.bg-primario{
    background-color: v.$primario;
}

.borde-box{
   // box-shadow: 0 0 .4rem .4rem rgba(1, 16, 39, 0.668);
    box-shadow: .3rem .3rem .4rem rgba(0,0,0,0.2),
    .2rem .1rem .1rem  rgb(89, 95, 113);
    
}

.open{ 
    position: relative;
    z-index: 5;
    display: block;
    transition: display 0.5s ease-out;
    transition: filter 0.3s ease-in-out;

    &:hover{
        filter: brightness(65%);
    }

}

.template-hidden{
    display: none;     
    transition: display 0.5s ease-out;
}

.card{
    a{

        &:hover{   
            position: relative;
    
        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            opacity: 0.4;
            box-shadow: 0 0 .4rem .4rem rgba(1, 16, 39, 0.668);
            border-radius: 1.5rem 1.5rem 0 0;
        }
        }
    }  
    figcaption{
        
        text-align: center;
        position: relative;
        color: v.$azul-claro;
        font-size: 1.5rem;
        background-color: v.$secundario-oscuro;
        margin: 0;
        padding: 0.5rem;
        box-shadow: .3rem .3rem .4rem rgba(0,0,0,0.2),
                    .2rem .1rem .1rem  rgba(89, 95, 113, 0.003);
        }
        img{
            box-shadow: .3rem .3rem .4rem rgba(0,0,0,0.2),
                        .2rem .1rem .1rem  rgba(89, 95, 113, 0.003);
            border-radius: 1.5rem 1.5rem 0 0;
            background-size: cover;
            width: 11rem;
        } 

        @include m.tablet { 
            img{
                width: 14rem;
            }
        }
}

.card-titulo{
    h3{
        color: v.$azul-claro;
        font-size: 1.5rem;
        background-color: #232734;
        margin: 0;
        padding: 0.5rem;      
        box-shadow: .3rem .0rem .2rem rgba(0,0,0,0.2),
                    .1rem .0rem .0rem  rgb(89, 95, 113);
    } 
}

.logo-type-normal {
    background-color: #232734;
    border-radius: 50%;
    width: 10rem;
    height: 10rem;
    margin: 1rem auto;
    margin-top: 3rem;
    img{
        width: 8.5rem;
        margin: 0 auto;
    }
    @include m.tablet { 
        margin: 1.5rem 0;
    }
    
}

