@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:math';
// .home{
//     min-height: calc(100vh - 17rem);
// }
.footer{
    margin-top: 15rem;
    position: relative;
    background: v.$secundario;  
    width: 100%;
    min-height: 10rem;
    padding: 2rem 5rem;
    display: flex;
    justify-content: center;
    align-items: center;       
    @include m.tablet{
        margin-top: 7.2rem;
    }
}
.footer-social{
    position: relative;
    display: flex;
    justify-content: center;
    margin: 1rem 0;
    padding: 0;
    flex-wrap: wrap;
    img{ 
        width: 4.5rem;
        transition: 0.5s;
        display: inline-block;
        &:hover{
            transform: translateY(-1rem);
        }
    }
}

.footer-menu{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    margin: 0;
    @include m.tablet{
        padding: 0 0 0 3rem;
    }
    .footer-menu-link{
        display: flex;
        img{
            position: relative;
            z-index: 15;
            transition: 0.5s;
            width: 4rem;
            margin: 0 .5rem;
            &:hover{
                transform: translateY(-1rem);
            }
        }
    }
    h3{
        margin: 1.3rem 0;
        color: v.$azul-claro;
        font-size: 2.5rem;
    }
    p{
        color: v.$azul-claro;
        margin: .9rem 0;
        font-size: 1.8rem;
        @include m.tablet{
            margin: .9rem 1.5rem;
        }
    }
    
}

.footer-menu-info{
    @include m.tablet {
     display: flex;
     flex-wrap: nowrap;
     margin: 3rem;
    }
}

.footer-copy{
    display: flex;
    justify-content: center;
    color: v.$azul-claro;
    font-size: 1.3rem;
}

.footer-border{ 
    margin: 1rem auto;
    width: 70%;
    border: .1rem solid v.$grisClaro;
}