@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    height: 7rem;
    background: v.$secundario;
    .header-inicio{ 
        display: flex;
        justify-content: flex-end;
        align-items: center;
        .header-span{
            margin-right: 3rem;
            color: v.$azul-claro;
            font-size: 1.7rem;
            font-weight: 700;
        }
        .header-img{
            padding: .8rem;
            margin-right: 3rem;
            width: 6rem;
            
        }
        .header-ubicacion{
            padding: .8rem;
            width: 5.5rem;
            transition: 0.5s;
        &:hover{
            transform: translateY(-.5rem);
            }
        }
   } 
}

