@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:math';

.contenido-principal{ 
    padding-top: 1rem;
    max-width: 90%;

    @include m.tablet {
        padding:6rem;
        margin-top: -10rem;
        position: relative;
        max-width: 70%;

    }
    
    @include m.xxl { 
        max-width: 120rem;
    } 
    
}

section:first-of-type {
    @include m.tablet {
        margin-top: -5rem;
    }
}

