@use 'base/variables' as v;
@use 'base/mixins' as m;

.header-pokemon{
    display: block;
    color: v.$azul-claro;
    @include m.tablet{
        display: flex;
    }
    .imagen-pokemon{
        img{
            border-radius: 50%;
            width: 15rem;
            margin: 0 auto;
            border: .3rem solid v.$secundario-oscuro;
        }
    }
}

.texto-pokemon{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: 2rem;
        @include m.tablet{
            align-items: flex-start;
        }
        .titulo-pokemon{
            display: flex;
            align-items: center;
            h3{
                margin-bottom: 0;
            }
            img{
                width: 4.5rem;
            }
            p{
                font-size: 5rem;
                color: v.$rangoS;
            }
        }
}