@use 'base/variables' as v;
@use 'base/mixins' as m;
.tier-main{
  background-image: linear-gradient(to bottom, v.$secundario 5%,v.$tipo-acero-dos 90%);
}

  .tier-table {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-top: 5rem;
    @include m.tablet{
      margin-left: 8rem;
    }
    @include m.desktop{
      margin-left: 20rem;
    }
  }
  
  .tier-rango-S{
    @include m.rango(v.$rangoS);
  }
  .tier-rango-A{
    @include m.rango(v.$rangoA);
  }
  .tier-rango-B{
    @include m.rango(v.$rangoB);
  }
  .tier-rango-C{
    @include m.rango(v.$rangoC);
  }
  .tier-rango-D{
    @include m.rango(v.$rangoD);
  }
  .tier-rango-F{
    @include m.rango(v.$rangoF);
  }
  .tier-rango-revert{
    @include m.tablet{
      display: revert;
    }
  }
  .tier-elementos {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: .5rem;
    margin: 0 auto;
    border-radius: 2rem;
    @include m.tablet{
      padding: 6rem;
      margin-right: 1rem;
    }
    @include m.desktop{
      padding: 10rem;
    }
    
  }

