@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:math';

.contenido-card{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    //gap: math.div( v.$separacion, 2);
    gap: 1.25rem;
    background-color: v.$secundario;
    padding: 2rem;
    border-radius: 1.25rem;
     box-shadow: .1rem .0rem .2rem rgba(0,0,0,0.2),
                .0rem .0rem .0rem  rgba(89, 95, 113, 0.003);
}

.multi-Type{   
        a{
        transition: filter 0.3s ease-in-out;
        &:hover{
        filter: brightness(70%);   
    }
        }
        figcaption{            
            border: thin solid v.$secundario-oscuro;
            border-top: none; 
        }    
}
.type-fuego{
    .card{
        @include m.type(v.$tipo-fuego, v.$tipo-fuego-dos)
    }  
}
