@use 'variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono ){
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet ){
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop ){
        @content;
    }
}
@mixin xxl {
    @media (min-width: v.$xxl ){
        @content;
    }
}

@mixin type($colorPrimario, $colorSecundario) {
    a{
        &::before{
            background: $colorPrimario;  /* fallback for old browsers */
                background: -webkit-linear-gradient(to right, $colorPrimario, $colorSecundario);  /* Chrome 10-25, Safari 5.1-6 */
                background: linear-gradient(to right, $colorPrimario, $colorSecundario); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
            @content;
        }
    }
    figcaption{            
        border: thin solid $colorPrimario;
        border-top: none; 
        @content;

    } 
}

@mixin rango($colorPrimario){
    display: flex;
    justify-content: center;
   p{
    margin: 3.1rem;
    font-size: 8rem;
    color: $colorPrimario;
    
   }
   @content;
}