@use 'variables' as v;


h1,h2,h3,figcaption{
    font-family: v.$fuente_principal;
    margin: 0 0 v.$separacion 0;
    text-align: center;
    line-height: 1.5;
}

h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;

    span{
        color: v.$primario;
        font-size: 1.8rem;
        //display: block;

    }

    &::after{
        content: '';
        display: block;
        width: 10rem;
        height: 10rem;
        margin: 0 auto;
    }
}
h3{
    font-size: 2.5rem;
}
h5{
    font-size: 1.2rem;
}
