@use 'variables' as v;

html {
    overflow-x: hidden;

    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
*,*:before, *:after {
    box-sizing: inherit;
}

body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    
    &::-webkit-scrollbar{
        width: 0.25rem;
    }
    &::-webkit-scrollbar-track {
        background: v.$secundario;
    }
    &::-webkit-scrollbar-thumb {
        background: v.$primario;
    }
}
// este html y body testean el footer para que se quede
// abajo si no sirve a borrar
html,body{
    overflow-x: hidden;
}

p {
    color: v.$negro;
    font-size: 2rem;
    margin: 0;
}

.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;   
}

a {
    text-decoration: none;
    cursor: pointer;
}

img {
    max-width: 100%;
    display: block;
}

figure{
    margin: 0.5rem;
}
li{
    list-style: none;
}
