@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:math';

.card-separador{
    @include m.tablet{
        display: flex;
        justify-content: space-between;

    }
}
.titulo-resistencias{
    margin: 5rem 0;
    span{      
        display: block;
    }
    @include m.tablet{
        span{
            display: initial;
        }
    }
}

.card-afinidad {
    position: relative;
    margin-bottom: 7rem;
    flex-direction: row;
    transition: .5s;
    height: 100%;
    .buff {
        display: flex;
        position: relative;
        width: 30rem;
        background: rgba(255, 255, 255, 0.10);
        backdrop-filter: blur(15px);
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border-radius: 1rem;
        margin: 0 auto;
    }

    .imgBx{
        position: absolute;
        top: 0;
        transform: translateY(-50%);
        width: 8.5rem;
        height: 8.5rem;
        
        
        overflow: hidden;
        transition: .5s;
        z-index: 10;
        border-radius: 50%;
        background: linear-gradient(to top, v.$tipo-acero-dos 35%, transparent 100%);
    }
    
}

.content-afinidad{
    position: relative;
    text-align: center;
    h3{
        
        margin-top: 6rem;
        font-size: 1.6rem;
        color: v.$azul-claro;
    }
    
}

.content-buff {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 3rem;
    width: 30rem;
    height: 0;
    overflow: hidden;
    transition: 0.5s;
    padding: 0;
    margin: 0 auto;
    li{
        width: 100%;
        background: rgba(255, 255, 255, 0.10);
        backdrop-filter: blur(15px);
        -webkit-backdrop-filter: blur(15px);
        border-radius: 1rem;
        display: flex;
        justify-content: center;
        transition: 0.5s;
        flex-wrap: wrap;
        margin-top: 1rem;
        opacity: 0;
        transform: scale(0);
        img{
            width: 4rem;
            height: 4rem;
        }
    }
}
.content-buff-debuff{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    .img-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;  
      }
    h5{
        color: v.$azul-claro;
        margin: 0.6rem 0;
        span{
            font-size: 2.5rem;
            padding-left: 1rem;
        }
        img{
            display: inline;
            width: 2rem;
            height: 2rem;
        }
    }
}


.toggle-ver{
    position: absolute;
    bottom: -1.5rem;
    width: 13rem;
    padding: .5rem 2rem;
    background: v.$azul-claro;
    border-radius: 3rem;
    border: .5rem solid v.$tipo-acero-dos;
    text-align: center;
    cursor: pointer;
    font-weight: 700;
    transition: .5s;
    &::before{
        content: 'Ver';
    }
}

.active-afinidad {
    height: 55rem; 
    transition-delay: 0s;
    
    .toggle-ver{
        background: v.$tipo-fuego;

        &::before{
            content: 'Close';
        }
    }
    .content-buff {
        transition-delay: .5s;
        height: 50rem;
        &:hover{
            li{
                opacity: .15;
                filter: blur(.2rem);
                transition-delay: 0s;
            }
        }
        li{
            opacity: 1;
            transform: scale(1);
            transition-delay: 0.4s;
            &:hover{
                opacity: 1;
                filter: blur(0);
                
            }
        }
    } 
}


