@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:math';

.build-main{
  background: v.$secundario-oscuro;
  border-radius: 1rem;
  position: relative;
  padding-bottom: 1rem;
}

.build-button{
  display: block;
  margin: 0 auto;
  margin-bottom: 2rem;
  margin-top: -1.3rem;
  background: v.$tipo-acero-dos;
  color: v.$azul-claro;
  width: 80%;
  height: 4rem;
  border-radius: 1.5rem 0;
  border: .2rem solid v.$secundario-oscuro;
  font-weight: bold;
  @include m.tablet{
    display: inline;
    width: 15rem;
  }
}
.btn-active{
  color: v.$secundario-oscuro;
  background: v.$primario;
} 

.build-container {
    display: none;
}
  
.build-container{
    &:first-of-type{
        display: block;
    }
}
  
.hidden {
    display: none;
}

.build-border{
    margin: 2rem auto;
    width: 70%;
    border: .1rem solid v.$grisClaro;
}
//-------------------------------

//--- Tabla ---------------------

.build-estadisticas {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 1.3rem;
  width: 100%;
  @include m.tablet {
    margin: 0 auto;
    width: 90%;
    font-size: 1.4rem;
  }
  
  table {
    width: 100%;
    text-align: center;
    border-collapse: collapse;
    margin-bottom: .8rem;
    
    img{ 
      display: initial;
      width: 1.8rem;
      margin-bottom: -.3rem;
      margin-left: -.3rem;
    }
    
    th {
      font-weight: bold;
      color: v.$azul-claro;
    }
    td {
      background-color: #f9f9f9;
      padding: .4rem;
      
    }
    tr{ 
      &:nth-child(even){
        td:not(.build-footer, .stats-base) {
          background-color: #f2f2f2;
        }
      }
    }
  }
}
.build-estadisticas{
  .stats-base{
    text-align: start;
    color: v.$azul-claro;
    width: 18%;
    background-color: v.$secundario-oscuro;
 }
}

  .build-footer{
    tr{
      td{
        background-color: v.$secundario-oscuro;
        color: v.$azul-claro;
      }
    }
  }

//--------------- items

.build-item-title{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    p{
      font-size: 1.5rem;
      margin-bottom: 1rem;
    }
  .build-item{
    
    img{
      width: 2.5rem;
      margin: 0 auto;
    }
  }
}

//-------- abilidad

.build-abildiad-title{
  display: flex;
  justify-content: space-around;
  width: 89%;
  p{
    font-size: 1.5rem;
  }
}

//----- movimientos

.build-movimientos {
  display: grid;
  grid-template-columns: 1fr 2fr; /* Dos columnas: la primera ocupa una fraccin de espacio, la segunda ocupa dos fracciones de espacio */
  grid-gap: .5rem;
  justify-items: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: v.$azul-claro;
}

// ------- otros equipos

.other-items{
  margin: 3rem 0;
  background-color: v.$secundario-oscuro;
  border-radius: 1rem;
  
  .title-items{
    display: flex;
    h3{
      font-size: 1.5rem;
      margin: -1rem 0 0 1rem ;
    
    }
  }
}

.contenedor-items{
  
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: .8rem;
  p{
    font-size: 1.5rem;
  }
  img{
    width: 3rem;
  }
}
// ------- otras abilidades
.other-ability{
  margin: 3rem 0;
  background-color: v.$secundario-oscuro;
  border-radius: 1rem;
  
  .title-ability{
    display: flex;
    h3{
      font-size: 1.5rem;
      margin: -1rem 0 0 1rem ;
    
    }
  }
}

.build-ability{
  display: flex;
  justify-content: space-evenly;
  padding: 1rem;
  justify-items: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: v.$azul-claro;

}
// ------- companeros

.content-friends{
    margin: 3rem 0;
    background-color: v.$secundario-oscuro;
    border-radius: 1rem;
    .title-friends{
    display: flex;
    h3{
      font-size: 1.5rem;
      margin: -1rem 0 0 1rem ;
    }
  }
}
.contenedor-friend{
  display: flex;
  padding: 1rem;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin: 1rem;
  .friends-nombre{
    p{
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1.5rem;
    }   
    img{
      border: .5rem solid v.$tipo-acero-dos;
      width: 9rem;
      border-radius: 50%;
      margin: 1rem;
    }
    &:hover{
      img{
        border: .5rem solid v.$grisClaro;
      }
      p{
        color: v.$primario;
      }  
    }
  }

}

// ------- conters

.content-counter{
  margin: 3rem 0;
  background-color: v.$secundario-oscuro;
  border-radius: 1rem;
  .title-friends{
  display: flex;
  h3{
    font-size: 1.5rem;
    margin: -1rem 0 0 1rem ;
  }
}
}
//--------separador footer

.separador-footer{
  height: 2rem;
}
