@use '../base/mixins' as m;
@use '../base/variables' as v;
@use 'sass:math';
//-------------------------  ResuableCss
.sidebar{
    .text{
        padding-left: 1.2rem;
        font-size: 1.5rem;
        font-weight: 700;
        color: v.$azul-claro;
        transition: all .3s ease-in-out;
        white-space: nowrap;
        opacity: 1;
    }
}


//-------------------------  SideBar
.sidebar {
    width: 100vw;
    position: fixed;
    background: v.$secundario;
    transition: all .3s ease-in-out;
    z-index: 99;
    padding: 1rem 1.2rem;
    top: 0;
    left: 0;
    @include m.tablet{
    height: 100%;
    width: 28rem;
    }
    header{
        position: relative;
        .toggle{
            margin: 0 auto;
            cursor: pointer;
            position: absolute;
            height: 4rem;
            width: 4rem;
            right: 0;
            top: 30%;
            //-----
            @include m.tablet{
                right: 0;
                top: 50%;
                right: -3.9rem;
                transform: translateY(-50%);
                height: 4rem;
                width: 4rem;
                display: flex;
                align-items: center;
                justify-content: center;
                }
        }
    }
}

.close{
    height: 10rem;
    width: 100vw;
    @include m.tablet{
     height: 100vh;
     width: 10rem;
    }
    .icon{
        display: none;
        @include m.tablet{
            display: block;
            margin-left: .6rem;
            margin-top: .3rem;
        }   
    }
    .text {
        opacity: 0;
    }
    .showOpen{
        opacity: 0;
    }
    //---- TEsT    
}

.image-text{
    display: flex;
    align-items: center;
    .image{
        max-width: 7rem; 
        display: flex;
        align-items: center; 
        @include m.tablet{
            min-width: 7.5rem;
        }   
    }

    .header-text{
        display: flex;
        flex-direction: column;
        margin: 0 2rem;

        .name{
            font-weight: 600;
        }
        .profession {
            margin-top: -2px;
        }
    }
}
//---- imagen aliniar al centro linea recta

.sidebar{
    
    ul{    
        padding: 0;
        @include m.tablet{
            padding-left: 0;
        }  
    }
    li{
        
        list-style: none;
        align-items: center;
        .icon{
            width: 5rem;
            margin: .5rem;
            @include m.tablet{
                width: 6rem;
            }  
            
        }
        a{
            display: flex;
            align-items: center;
        }
        .iocn-link{
            margin-top: 3rem;
            margin-bottom: 3rem;
            width: 100%;
            display: flex;
            padding-right: 2rem;
            border-radius: 1rem;
            align-items: center;
            justify-content: space-between;
            transition: all .5s;
            @include m.tablet {    
                width: 90%;
            }
            &:hover{
                background: v.$grisClaro;
                transform: translateY(-.5rem);

            }
            .showOpen{
                width: 3rem;
                cursor: pointer;
            }
        }
    }
}
.sub-menu{
    background: v.$grisClaro;
    border-radius: .8rem;
    display: none;
    .sub-contenedor{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        img{
            border: thin solid v.$grisClaro;
            border-radius: .8rem;
            width: 4rem;
            margin: .3rem; 
            padding: .1rem;
            &:hover{
                background: v.$secundario;
            }
        }
        
    }
    .sub-contenedor-contacto{
        img{
            border: thin solid v.$grisClaro;
            border-radius: .8rem;
            width: 7rem;
            margin: .7rem; 
            padding: .3rem;
            &:hover{
                background: v.$secundario;
            }
        }
    }
    
}

.closeSub{
    display: block;
}

.menu-links{
    margin-top: 4rem;
    
    .nav-link{  
        margin-bottom: 3rem;   
        a{
            height: 100%;
            width: 100%;
            display: flex;
            align-items: center;
            text-decoration: none;
            border-radius: .8rem;
            transition: all .3s ease-in-out;

            @include m.tablet {    
                width: 90%;
            }
            &:hover{
                background: v.$grisClaro;
                &:hover{
                    transform: translateY(-.5rem);
                    }
            }
        }
    }
}
//----- menu 

.menu-bar{
        height: calc(100% - 15rem);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .mode{
            display: flex;
            position: relative;
            border-radius: .5rem;
            background-color: v.$grisClaro;
            .moon-sun{
                position: relative;
                height: 6rem;
                width: 6rem;
                display: flex;
                align-items: center;
                
            }   
        }  
}

.menu-bar{
    display: none;
    
    &.show{
        display: flex;
    }
    @include m.tablet{
        display: flex;
    }
    .mode{
        img{
            position: absolute;
        }    
        .sun{
            opacity: 0;
        }
    }
}


.menu-bar{
        .toggle-switch{
            position: absolute;
            right: 0;
            display: flex;
            align-items: center;
           // justify-content: center;
            height: 100%;
            min-width: 7rem;
            cursor: pointer;
            .switch{
                position: relative;
                height: 2.4rem;
                width: 6rem;
                border-radius: 2.5rem;
                background: v.$secundario-oscuro;
                
                &::before{
                    content: '';
                    position: absolute;
                    height: 1.3rem;
                    width: 1.3rem;
                    border-radius: 50%;
                    top: 50%;
                    left: .4rem;
                    transform: translateY(-50%);
                    background: v.$azul-claro;
                    transition: all .3s ease;
                }
                
            }
            
        }
}

body.dark{
    .toggle-switch{
        .switch{
            &::before {
                left: 4rem;
            }
        }
    }
}
body.dark {
    .sidebar {
        background: #f12711;  /* fallback for old browsers */
        background: -webkit-linear-gradient(to bottom, #f5af19, #f12711);  /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to bottom, #f5af19, #f12711); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        .text{
            color: v.$secundario;
        }
        .mode{
            background-color:v.$grisClaro;    
        }
        .nav-link{
        a{
            &:hover{
            background: v.$grisClaro;
            }
        }
    }
    .sub-contenedor-contacto{
        img{
            
            &:hover{
                background: v.$grisClaro;
            }
        }
    }
  }
  .sun{
    opacity: 1;
  }
}
body.dark {
    .menu-bar{
        .mode{
            .sun{
                position: absolute;
                opacity: 1;
              }
            .moon{
                opacity: 0;
            }
        }
    } 
}

body.dark{
    .header{
        background: #f5af19;
        .header-span{
            color: v.$secundario-oscuro;
        }
    }
}
