html {
  overflow-x: hidden;
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}
body::-webkit-scrollbar {
  width: 0.25rem;
}
body::-webkit-scrollbar-track {
  background: #232734;
}
body::-webkit-scrollbar-thumb {
  background: rgb(255, 208, 0);
}

html, body {
  overflow-x: hidden;
}

p {
  color: #000;
  font-size: 2rem;
  margin: 0;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

figure {
  margin: 0.5rem;
}

li {
  list-style: none;
}

.borde {
  border-bottom: 1rem solid #232734;
  width: 100%;
  margin-top: 5rem;
  margin-bottom: -6.5rem;
}

.border-top {
  border-top: none;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: rgb(255, 208, 0);
}

.borde-box {
  box-shadow: 0.3rem 0.3rem 0.4rem rgba(0, 0, 0, 0.2), 0.2rem 0.1rem 0.1rem rgb(89, 95, 113);
}

.open {
  position: relative;
  z-index: 5;
  display: block;
  transition: display 0.5s ease-out;
  transition: filter 0.3s ease-in-out;
}
.open:hover {
  filter: brightness(65%);
}

.template-hidden {
  display: none;
  transition: display 0.5s ease-out;
}

.card a:hover {
  position: relative;
}
.card a:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.4;
  box-shadow: 0 0 0.4rem 0.4rem rgba(1, 16, 39, 0.668);
  border-radius: 1.5rem 1.5rem 0 0;
}
.card figcaption {
  text-align: center;
  position: relative;
  color: #e4e4fa;
  font-size: 1.5rem;
  background-color: #1b1d27;
  margin: 0;
  padding: 0.5rem;
  box-shadow: 0.3rem 0.3rem 0.4rem rgba(0, 0, 0, 0.2), 0.2rem 0.1rem 0.1rem rgba(89, 95, 113, 0.003);
}
.card img {
  box-shadow: 0.3rem 0.3rem 0.4rem rgba(0, 0, 0, 0.2), 0.2rem 0.1rem 0.1rem rgba(89, 95, 113, 0.003);
  border-radius: 1.5rem 1.5rem 0 0;
  background-size: cover;
  width: 11rem;
}
@media (min-width: 768px) {
  .card img {
    width: 14rem;
  }
}

.card-titulo h3 {
  color: #e4e4fa;
  font-size: 1.5rem;
  background-color: #232734;
  margin: 0;
  padding: 0.5rem;
  box-shadow: 0.3rem 0rem 0.2rem rgba(0, 0, 0, 0.2), 0.1rem 0rem 0rem rgb(89, 95, 113);
}

.logo-type-normal {
  background-color: #232734;
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
  margin: 1rem auto;
  margin-top: 3rem;
}
.logo-type-normal img {
  width: 8.5rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .logo-type-normal {
    margin: 1.5rem 0;
  }
}

h1, h2, h3, figcaption {
  font-family: "Roboto", sans-serif;
  margin: 0 0 5rem 0;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}
h2 span {
  color: rgb(255, 208, 0);
  font-size: 1.8rem;
}
h2::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
}

h3 {
  font-size: 2.5rem;
}

h5 {
  font-size: 1.2rem;
}

.header {
  height: 7rem;
  background: #232734;
}
.header .header-inicio {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.header .header-inicio .header-span {
  margin-right: 3rem;
  color: #e4e4fa;
  font-size: 1.7rem;
  font-weight: 700;
}
.header .header-inicio .header-img {
  padding: 0.8rem;
  margin-right: 3rem;
  width: 6rem;
}
.header .header-inicio .header-ubicacion {
  padding: 0.8rem;
  width: 5.5rem;
  transition: 0.5s;
}
.header .header-inicio .header-ubicacion:hover {
  transform: translateY(-0.5rem);
}

.sidebar .text {
  padding-left: 1.2rem;
  font-size: 1.5rem;
  font-weight: 700;
  color: #e4e4fa;
  transition: all 0.3s ease-in-out;
  white-space: nowrap;
  opacity: 1;
}

.sidebar {
  width: 100vw;
  position: fixed;
  background: #232734;
  transition: all 0.3s ease-in-out;
  z-index: 99;
  padding: 1rem 1.2rem;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    width: 28rem;
  }
}
.sidebar header {
  position: relative;
}
.sidebar header .toggle {
  margin: 0 auto;
  cursor: pointer;
  position: absolute;
  height: 4rem;
  width: 4rem;
  right: 0;
  top: 30%;
}
@media (min-width: 768px) {
  .sidebar header .toggle {
    right: 0;
    top: 50%;
    right: -3.9rem;
    transform: translateY(-50%);
    height: 4rem;
    width: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.close {
  height: 10rem;
  width: 100vw;
}
@media (min-width: 768px) {
  .close {
    height: 100vh;
    width: 10rem;
  }
}
.close .icon {
  display: none;
}
@media (min-width: 768px) {
  .close .icon {
    display: block;
    margin-left: 0.6rem;
    margin-top: 0.3rem;
  }
}
.close .text {
  opacity: 0;
}
.close .showOpen {
  opacity: 0;
}

.image-text {
  display: flex;
  align-items: center;
}
.image-text .image {
  max-width: 7rem;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .image-text .image {
    min-width: 7.5rem;
  }
}
.image-text .header-text {
  display: flex;
  flex-direction: column;
  margin: 0 2rem;
}
.image-text .header-text .name {
  font-weight: 600;
}
.image-text .header-text .profession {
  margin-top: -2px;
}

.sidebar ul {
  padding: 0;
}
@media (min-width: 768px) {
  .sidebar ul {
    padding-left: 0;
  }
}
.sidebar li {
  list-style: none;
  align-items: center;
}
.sidebar li .icon {
  width: 5rem;
  margin: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar li .icon {
    width: 6rem;
  }
}
.sidebar li a {
  display: flex;
  align-items: center;
}
.sidebar li .iocn-link {
  margin-top: 3rem;
  margin-bottom: 3rem;
  width: 100%;
  display: flex;
  padding-right: 2rem;
  border-radius: 1rem;
  align-items: center;
  justify-content: space-between;
  transition: all 0.5s;
}
@media (min-width: 768px) {
  .sidebar li .iocn-link {
    width: 90%;
  }
}
.sidebar li .iocn-link:hover {
  background: rgba(236, 238, 239, 0.1254901961);
  transform: translateY(-0.5rem);
}
.sidebar li .iocn-link .showOpen {
  width: 3rem;
  cursor: pointer;
}

.sub-menu {
  background: rgba(236, 238, 239, 0.1254901961);
  border-radius: 0.8rem;
  display: none;
}
.sub-menu .sub-contenedor {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.sub-menu .sub-contenedor img {
  border: thin solid rgba(236, 238, 239, 0.1254901961);
  border-radius: 0.8rem;
  width: 4rem;
  margin: 0.3rem;
  padding: 0.1rem;
}
.sub-menu .sub-contenedor img:hover {
  background: #232734;
}
.sub-menu .sub-contenedor-contacto img {
  border: thin solid rgba(236, 238, 239, 0.1254901961);
  border-radius: 0.8rem;
  width: 7rem;
  margin: 0.7rem;
  padding: 0.3rem;
}
.sub-menu .sub-contenedor-contacto img:hover {
  background: #232734;
}

.closeSub {
  display: block;
}

.menu-links {
  margin-top: 4rem;
}
.menu-links .nav-link {
  margin-bottom: 3rem;
}
.menu-links .nav-link a {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  text-decoration: none;
  border-radius: 0.8rem;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .menu-links .nav-link a {
    width: 90%;
  }
}
.menu-links .nav-link a:hover {
  background: rgba(236, 238, 239, 0.1254901961);
}
.menu-links .nav-link a:hover:hover {
  transform: translateY(-0.5rem);
}

.menu-bar {
  height: calc(100% - 15rem);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.menu-bar .mode {
  display: flex;
  position: relative;
  border-radius: 0.5rem;
  background-color: rgba(236, 238, 239, 0.1254901961);
}
.menu-bar .mode .moon-sun {
  position: relative;
  height: 6rem;
  width: 6rem;
  display: flex;
  align-items: center;
}

.menu-bar {
  display: none;
}
.menu-bar.show {
  display: flex;
}
@media (min-width: 768px) {
  .menu-bar {
    display: flex;
  }
}
.menu-bar .mode img {
  position: absolute;
}
.menu-bar .mode .sun {
  opacity: 0;
}

.menu-bar .toggle-switch {
  position: absolute;
  right: 0;
  display: flex;
  align-items: center;
  height: 100%;
  min-width: 7rem;
  cursor: pointer;
}
.menu-bar .toggle-switch .switch {
  position: relative;
  height: 2.4rem;
  width: 6rem;
  border-radius: 2.5rem;
  background: #1b1d27;
}
.menu-bar .toggle-switch .switch::before {
  content: "";
  position: absolute;
  height: 1.3rem;
  width: 1.3rem;
  border-radius: 50%;
  top: 50%;
  left: 0.4rem;
  transform: translateY(-50%);
  background: #e4e4fa;
  transition: all 0.3s ease;
}

body.dark .toggle-switch .switch::before {
  left: 4rem;
}

body.dark .sidebar {
  background: #f12711; /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #f5af19, #f12711); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #f5af19, #f12711); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
body.dark .sidebar .text {
  color: #232734;
}
body.dark .sidebar .mode {
  background-color: rgba(236, 238, 239, 0.1254901961);
}
body.dark .sidebar .nav-link a:hover {
  background: rgba(236, 238, 239, 0.1254901961);
}
body.dark .sidebar .sub-contenedor-contacto img:hover {
  background: rgba(236, 238, 239, 0.1254901961);
}
body.dark .sun {
  opacity: 1;
}

body.dark .menu-bar .mode .sun {
  position: absolute;
  opacity: 1;
}
body.dark .menu-bar .mode .moon {
  opacity: 0;
}

body.dark .header {
  background: #f5af19;
}
body.dark .header .header-span {
  color: #1b1d27;
}

.img-full {
  background-image: linear-gradient(to left, rgba(26, 29, 39, 0.6470588235), transparent), linear-gradient(to right, rgba(26, 29, 39, 0.7529411765), transparent), url(../../../build/img/poke_01.png);
  background-size: auto;
}

.style {
  background-image: linear-gradient(to bottom, #232734 5%, #283048 85%, #232734 15%);
}

.main-container {
  position: relative;
  height: 60vh;
  margin: 0;
}

.container {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 80%;
  height: 85%;
  transform: translate(-50%, -50%);
  padding: 5rem;
}
.container#slide {
  width: max-content;
  margin-top: 5rem;
}

.container-sub .item {
  width: 20rem;
  height: 30rem;
  background-position: 50% 50%;
  display: inline-block;
  transition: 0.5s;
  background-size: cover;
  position: absolute;
  z-index: 50;
  top: 50%;
  transform: translate(0, -50%);
  border-radius: 2rem;
  box-shadow: 0.3rem 0.3rem 0.4rem rgba(0, 0, 0, 0.2), 0.2rem 0.1rem 0.1rem rgb(89, 95, 113);
}
.container-sub .item:nth-child(1), .container-sub .item:nth-child(2) {
  left: 0;
  top: 0;
  transform: translate(0, 0);
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  box-shadow: 0.3rem 0.3rem 0.4rem rgba(0, 0, 0, 0.2), 0.2rem 0.1rem 0.1rem rgb(89, 95, 113);
}
.container-sub .item:nth-child(3) {
  left: 50%;
}
.container-sub .item:nth-child(4) {
  left: calc(50% + 21rem);
}
.container-sub .item:nth-child(5) {
  left: calc(50% + 42rem);
}
.container-sub .item:nth-child(n+6) {
  left: calc(50% + 63rem);
  opacity: 0;
}

.fondo-1 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.2)), url(../../../build/img/fondo_tierlist.jpg);
}

.fondo-2 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.2)), url(../../../build/img/fondo_inicio.jpg);
}

.fondo-3 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.2)), url(../../../build/img/fondo_listapokemon.jpg);
}

.content {
  display: none;
  width: 20rem;
  padding: 0.7rem;
  position: absolute;
  top: 20%;
  color: #e4e4fa;
}
@media (min-width: 768px) {
  .content {
    top: 50%;
    left: 5rem;
    width: 30rem;
    text-align: left;
    padding: 0;
    transform: translate(0, -50%);
  }
}

.container-sub .item:nth-child(2) .content {
  display: block;
  z-index: 55;
}
.container-sub .item .name {
  font-size: 2rem;
  font-weight: bold;
  opacity: 0;
  animation: showContent 1s ease-in-out 1 forwards;
}
@media (min-width: 768px) {
  .container-sub .item .name {
    font-size: 4rem;
  }
}
.container-sub .item .des {
  width: 80%;
  color: #e4e4fa;
  margin: 2rem 0;
  opacity: 0;
  font-size: 1.5rem;
  animation: showContent 1s ease-in-out 0.3s 1 forwards;
}
@media (min-width: 768px) {
  .container-sub .item .des {
    font-size: 4rem;
    width: 100%;
  }
}
.container-sub .item .buttons {
  position: relative;
  padding: 1rem 1rem;
  opacity: 0;
  animation: showContent 1s ease-in-out 0.6s 1 forwards;
}
@media (min-width: 768px) {
  .container-sub .item .buttons a {
    display: block;
    width: 100%;
  }
}

@keyframes showContent {
  from {
    opacity: 0;
    transform: translate(0, 10rem);
    filter: blur(3.1rem);
  }
  to {
    opacity: 1;
    transform: translate(0, 0);
    filter: blur(0);
  }
}
.buttons {
  position: absolute;
  bottom: 2rem;
  z-index: 60;
}
@media (min-width: 768px) {
  .buttons {
    bottom: 3rem;
    text-align: center;
    width: 100%;
  }
}
.buttons a {
  position: relative;
  display: inline-block;
  background: #fff;
  width: 7rem;
  height: 3rem;
  margin: 1rem 1rem 0 1rem;
}
@media (min-width: 768px) {
