@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:math';


.style{
    background-image: linear-gradient(to bottom, v.$secundario 5%,v.$tipo-acero-dos 85%, v.$secundario 15%);
}
.main-container {
    position: relative;
    height: 60vh;
    margin: 0;
  }
.container{
    position: absolute;
    left: 50%;
    top: 50%;
    width: 80%;
    height: 85%;
    transform: translate(-50%,-50%);
    padding: 5rem;
    &#slide{
        width:max-content;
        margin-top: 5rem;
    } 
}

.container-sub{
    .item{
        width: 20rem;
        height: 30rem;
        background-position: 50% 50%;
        display: inline-block;
        transition:  0.5s;
        background-size: cover;
        position: absolute;
        z-index: 50;
        top: 50%;
        transform: translate(0, -50%);
        border-radius: 2rem;
        box-shadow: .3rem .3rem .4rem rgba(0,0,0,0.2),
        .2rem .1rem .1rem  rgb(89, 95, 113);
        &:nth-child(1),
        &:nth-child(2){
            left: 0;
            top: 0;
            transform: translate(0,0);
            width: 100%;
            height: 100%;
            border-radius: 2rem;
            box-shadow: .3rem .3rem .4rem rgba(0,0,0,0.2),
            .2rem .1rem .1rem  rgb(89, 95, 113);
        }
        &:nth-child(3){
            left: 50%;
            
        }
        &:nth-child(4){
            left: calc(50% + 21rem);
        }
        &:nth-child(5){
            left: calc(50% + 42rem);
        }
        &:nth-child(n+6){
            left: calc(50% + 63rem);
            opacity: 0;
        }
    }
}


.fondo-1{
    background-image: linear-gradient(to right, rgb(0 0 0 / .4),
    rgb(0 0 0 / .2)), url(../../../build/img/fondo_tierlist.jpg);
}
.fondo-2{
    background-image: linear-gradient(to right, rgb(0 0 0 / .4),
    rgb(0 0 0 / .2)), url(../../../build/img/fondo_inicio.jpg);
}
.fondo-3{
    background-image: linear-gradient(to right, rgb(0 0 0 / .4),
    rgb(0 0 0 / .2)), url(../../../build/img/fondo_listapokemon.jpg);
}
.content{
    display: none;
    width: 20rem;
    padding: 0.7rem;
    position: absolute;
    top: 20%;
    color: v.$azul-claro;
    @include m.tablet{
    top: 50%;
    left: 5rem;
    width: 30rem;
    text-align: left;
    padding: 0;
    transform: translate(0,-50%);
    }
}

.container-sub{
    
    .item{
        &:nth-child(2){
            .content{
                display: block;
                z-index: 55;
            }       
        }
    .name{
        font-size: 2rem;
        font-weight: bold;
        opacity: 0;
        
        animation: showContent 1s ease-in-out 1 forwards;
        @include m.tablet{
            font-size: 4rem;
            }
    }
    .des{
       width:80% ;
       color: v.$azul-claro;
       margin: 2rem 0;
       opacity: 0; 
       font-size: 1.5rem;
       animation: showContent 1s ease-in-out 0.3s 1 forwards;
       @include m.tablet{
        font-size: 4rem;
        width: 100%;
        }
    }
    .buttons{
        position: relative;
        padding: 1rem 1rem;
        opacity: 0;
        animation: showContent 1s ease-in-out 0.6s 1 forwards;
        @include m.tablet{
            a{
                display: block;
                width: 100%;
            }
        }
    }
    }
}

@keyframes showContent {
    from{
        opacity: 0;
        transform: translate(0,10rem);
        filter: blur(3.1rem);
    }
    to{
        opacity: 1;
        transform: translate(0,0);
        filter: blur(0);
    }
}

.buttons{
    position: absolute;
    bottom: 2rem;
    z-index: 60;
    @include m.tablet{
        bottom: 3rem;
        text-align: center;
        width: 100%;
    }
    a{
        position: relative;
        display: inline-block;
        background: #fff;
        width: 7rem;
        height: 3rem;
        margin: 1rem 1rem 0 1rem;
        @include m.tablet{
            margin: 2rem;
            width: 10rem;
            height: 4rem;
            
        }
        &::before,&::after{
            content: '';
            position: absolute;
            inset: 0;
            transition: 0.5s;
        }
        &:nth-child(1)::before,
        &:nth-child(1)::after{
            background: linear-gradient(45deg, #00ccff, #0e1538,
            #0e1538, #d400d4);
        }
        &:nth-child(2)::before,
        &:nth-child(2)::after{
            background: linear-gradient(45deg, #ff075b, #0e1538,
            #0e1538, #1aff22);
        }
        &:hover{

            &::before{
                inset: -.3rem;
            }
            &::after{
                inset: -.3rem;
            }
        }
            
    }
    span{
        display: flex;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: v.$secundario;
        z-index: 10;
        align-items: center;
        justify-content: center;
        font-size: 1rem;
        text-transform: uppercase;
        color: v.$primario;
        overflow: hidden;
        @include m.tablet{
            font-size: 1.5rem;
        }
        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: -50%;
            width: 100%;
            height: 100%;
            background: v.$grisClaro;
            transform: skew(25deg);
        }
        
    }
}

